%h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

%headerText {
  font-family: "Roboto", sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #fff;
}

%mainParagraphText {
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;
  color: $textMainColor;
}
